    color: white;
}

div.contact {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 2rem;
    margin: 5rem 25%;
    border-radius: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5), inset 0 0 10px rgba(0, 0, 0, 0.3);
    gap: 2rem;
}

div.contact:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.8), inset 0 0 10px rgba(0, 0, 0, 0.3);
}

div.details {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

h1, p {
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
}

h1.name {
    font-size: 2rem;
}

p.detail {
    font-size: 1.5rem;
}

.img-container {
    border-radius: 1rem;
    max-width: 250px;
    position: relative;
    display: inline-block;
    overflow: hidden;
    border: 5px solid #ffffff;
}

.img-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow:
        inset 4px 4px 8px rgba(0, 0, 0, 0.3),
        inset -4px -4px 8px rgba(0, 0, 0, 0.3);
    pointer-events: none;
}

img {
    border-radius: 1rem;
    width: 100%;
    height: auto;
